import React, { useEffect } from "react";
import NikeLogo from "../assets/favicon.ico";
import { useDispatch, useSelector } from "react-redux";
import {
  createOrderWithCardAsync,
  selectCurrentOrder,
  selectOrderCardPaymentData,
  validateOrderWithCardAsync,
} from "../features/order/orderSlice";
import { useNavigate } from "react-router-dom";

const CheckoutCardPayment = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const cardPaymentData = useSelector(selectOrderCardPaymentData);
  const currentOrder = useSelector(selectCurrentOrder);
  const razorPayData = (data) => {
    console.log(process.env.REACT_APP_RAZORPAY_KEY_ID);
    console.log(data.order);
    const options = {
      key: process.env.REACT_APP_RAZORPAY_KEY_ID, // Enter the Key ID generated from the Dashboard
      amount: data.order.amount, // Amount is in currency subunits. Default currency is INR. Hence, 50000 refers to 50000 paise
      currency: data.order.currency,
      name: "NIKE",
      description: "Test Transaction",
      image: NikeLogo,
      order_id: data.order.id, //This is a sample Order ID. Pass the `id` obtained in the response of Step 1
      handler: function (response) {
        navigate(`/orderSuccess/${currentOrder.id}`);
      },
      prefill: {
        name: "Gaurav Kumar",
        email: "gaurav.kumar@example.com",
        contact: "9000090000",
      },
      notes: {
        address: "Razorpay Corporate Office",
        orderId: currentOrder.id,
      },
      theme: {
        color: "#000",
      },
    };
    const rzp = new window.Razorpay(options);
    rzp.open();
    rzp.on("payment.failed", function (response) {
      navigate("/paymentFailed");
    });
  };
  useEffect(() => {
    if (currentOrder) {
      dispatch(createOrderWithCardAsync(currentOrder));
    }
  }, [dispatch, currentOrder]);
  useEffect(() => {
    if (cardPaymentData) {
      console.log("going to work");
      razorPayData(cardPaymentData);
      console.log("Worked");
    }
  }, [cardPaymentData]);
  return <div>Processing your payment</div>;
};

export default CheckoutCardPayment;
